define("appkit/views/popover-view",
  [],
  function() {
    "use strict";
    var PopoverView;

    PopoverView = Ember.View.extend({
      tagName: "span",
      classNames: ["description"],
      didInsertElement: function() {
        return this.$("button").popover();
      }
    });


    return PopoverView;
  });