define("appkit/routes/index",
  ["appkit/models/deal"],
  function(Deal) {
    "use strict";
    var IndexRoute;

    IndexRoute = Ember.Route.extend({
      redirect: function() {
        var deal;
        deal = Deal.random();
        return this.transitionTo("/hands/" + (deal.id.toString()));
      }
    });


    return IndexRoute;
  });