define("appkit/models/card",
  [],
  function() {
    "use strict";
    var Card;

    Card = Ember.Object.extend({
      value: (function() {
        return this.get("content")[1];
      }).property("content"),
      suit: (function() {
        return this.get("content")[0];
      }).property("content"),
      toString: function() {
        return this.get("content");
      }
    });


    return Card;
  });