define("appkit/app",
  ["resolver","appkit/routes"],
  function(Resolver, routes) {
    "use strict";
    var BP;

    Ember.LOG_VERSION = false;

    BP = Ember.Application.create({
      rootElement: "#bridge-points",
      modulePrefix: "appkit",
      Resolver: Resolver
    });


    BP.Router.map(routes);


    return BP;
  });